import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import ThemeProvider from 'styled-components'
import theme from "../utils/theme.js"
import breakpoint from 'styled-components-breakpoint'

import {Button, PhotoWrapper} from '../utils/styled-components.js'
import { translate } from "react-i18next"

//stlyes
const MainWrapper = styled.div`
  height: 55vh;
`
const PhotoWrapperIndex = PhotoWrapper.extend`
  background:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
    url('http://res.cloudinary.com/ds0f4qyyt/image/upload/v1492714068/baby_minified.jpg');
`
const MainContent = styled.div`
  z-index: 99;
  margin: 0 auto;
  display: flex;
  max-width: 960px;
  font-size: 1.25rem;
  flex-flow: column;
  text-align: center;
  justify-content: center;
  padding: 0rem 1.45rem;
  padding-top: 4rem;
  color: ${props => props.theme.color.l};
`
const OptionContainer = styled.div`
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  max-width: 50rem;
`
const Option = styled.div`
  display:  flex;
  flex-flow: column;
  & > label{
    font-weight: bold;
  }
`
const RegButton = Button.extend`
  margin: 1.45rem .1rem;
  width: 8rem;
  height: 4rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 12rem;
  `}
`
const Label = styled.label`
  font-size: 1.125rem;
  font-weight: bold;
  ${props => breakpoint('md', props.theme.breakpoints)`
    font-size: 1.5rem;
  `}
`
const Header = styled.h1`
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  ${props => breakpoint('md', props.theme.breakpoints)`
    font-size: 3.5rem;
  `}
`
const Description = styled.p`
  font-size: 1;
  line-height: 1.4rem;
  ${props => breakpoint('md', props.theme.breakpoints)`
    padding: 1rem 10%;
    font-size: 1.5rem;
    line-height: 1.8rem;
  `}
`
const SubHeader = styled.h2`
  font-size: 1.5rem;
  font-weight: bold;
  color: ${props => props.theme.color.s};
  ${props => breakpoint('md', props.theme.breakpoints)`
    font-size: 2rem;
    padding: 2rem 0;
  `}
`
const IndexPage = ({t}) => (
  <MainWrapper>
    <PhotoWrapper/>
    <MainContent>
      <Header>{t("home.header")}</Header>
      <Description>{t("home.subheader")}</Description>
      <SubHeader>{t('home.callToAction')}</SubHeader>
      <OptionContainer>
        <Option>
          <Label htmlFor="patients" >{t('home.patients')}</Label>
          <Link id="patients" to="/page-2/">
          <RegButton>
          {t('home.patientButton')}
          </RegButton>
          </Link>
        </Option>
        <Option>
          <Label htmlFor="organizations" >{t('home.organizations')}</Label>
          <Link id="organizations" to="/page-2/">
          <RegButton>
          {t('home.organizationButton')}
          </RegButton>
          </Link>
        </Option>
      </OptionContainer>
    </MainContent>
  </MainWrapper>
)

export default translate()(IndexPage)



